export default {
	props: {
		background:{
			type : String,
			default : "#F8F8F8"
		},
		height:{
			type : Number,
			default : 90
		},
		haveStatusBar:{
			type : Boolean,
			default : true
		},
		isSeize : {
			type:Boolean,
			default : true
		}
	},
	data(){
		return{
			top : 0,
			paddingTop : 0,
			BoundingWidth :'0px'
		}
	},
	created:function(){
		if(!this.haveStatusBar){return ;}
		var res  = uni.getSystemInfoSync();
		this.top = res.statusBarHeight;
		this.paddingTop = this.top + uni.upx2px(this.height); 

		// 小程序胶囊按钮
		var res  = wx.getMenuButtonBoundingClientRect();
		this.BoundingWidth = (res.width + 20) + 'px';

	}
}
